'use client';

import { useState } from 'react';

const Header = () => {
  const [activeLink, setActiveLink] = useState('Home');
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const navLinks = ['Home', 'About Us', 'Services', 'Process', 'Contact'];

  return (
    <header className="w-full bg-white border-b-[0.2px] border-[#130f78] rounded-tl-[11px] rounded-tr-[11px] sticky top-0 z-[1000]">
      <div className="max-w-[1728px] mx-auto px-6 lg:px-12 xl:px-16 py-2 lg:py-3 flex items-center justify-between gap-8 lg:gap-12">
        <div className="flex-shrink-0">
          <div className="w-10 h-10 md:w-12 md:h-12 bg-[#130f78] rounded-[4px] flex items-center justify-center cursor-pointer transition-transform duration-200 hover:scale-105">
            <span className="text-white text-lg md:text-xl font-bold font-lato tracking-tight">DS</span>
          </div>
        </div>

        <nav className="hidden md:flex items-center gap-8 lg:gap-10 xl:gap-12 flex-1 justify-center">
          {navLinks.map((link) => (
            <a
              key={link}
              href={`#${link.toLowerCase().replace(' ', '-')}`}
              className={`text-lg font-semibold font-lato transition-colors duration-200 relative py-2 ${
                activeLink === link 
                  ? 'text-[#130f78]' 
                  : 'text-black hover:text-[#130f78]'
              }`}
              onClick={() => setActiveLink(link)}
            >
              {link}
            </a>
          ))}
        </nav>

        <button
          type="button"
          className="md:hidden bg-transparent border-none cursor-pointer p-2 flex flex-col justify-center items-center w-10 h-10 z-50 relative touch-manipulation"
          onClick={(e) => {
            e.stopPropagation();
            setIsMobileMenuOpen(prev => !prev);
          }}
          aria-label="Toggle menu"
          aria-expanded={isMobileMenuOpen}
        >
          <span className="flex flex-col justify-between w-6 h-[18px] relative">
            <span className={`block h-0.5 w-full bg-black rounded transition-all duration-300 ${
              isMobileMenuOpen ? 'rotate-45 translate-y-2' : ''
            }`}></span>
            <span className={`block h-0.5 w-full bg-black rounded transition-all duration-300 ${
              isMobileMenuOpen ? 'opacity-0' : ''
            }`}></span>
            <span className={`block h-0.5 w-full bg-black rounded transition-all duration-300 ${
              isMobileMenuOpen ? '-rotate-45 -translate-y-2' : ''
            }`}></span>
          </span>
        </button>

        <div className="flex-shrink-0 hidden md:block">
          <button className="bg-[#daff35] text-[#130f78] border-none rounded-[4px] px-5 py-2.5 lg:px-6 lg:py-3 text-lg font-bold font-lato cursor-pointer transition-all duration-200 whitespace-nowrap hover:bg-[#c4ef00] hover:-translate-y-0.5 hover:shadow-[0_4px_8px_rgba(218,255,53,0.3)] active:translate-y-0">
            Get Started
          </button>
        </div>
      </div>

      <div className={`md:hidden w-full transition-all duration-300 ease-in-out ${
        isMobileMenuOpen ? 'max-h-[600px] opacity-100' : 'max-h-0 opacity-0 overflow-hidden'
      }`}>
        <nav className="flex flex-col bg-white border-t-[0.2px] border-[#130f78] px-6 gap-4 py-4">
          {navLinks.map((link) => (
            <a
              key={link}
              href={`#${link.toLowerCase().replace(' ', '-')}`}
              className={`text-base font-medium font-lato transition-colors duration-200 py-3 border-b border-[#f0f0f0] last:border-b-0 ${
                activeLink === link 
                  ? 'text-[#130f78]' 
                  : 'text-black hover:text-[#130f78]'
              }`}
              onClick={() => {
                setActiveLink(link);
                setIsMobileMenuOpen(false);
              }}
            >
              {link}
            </a>
          ))}
          <button className="bg-[#daff35] text-[#130f78] border-none rounded-[4px] px-5 py-2.5 text-lg font-bold font-lato cursor-pointer transition-all duration-200 mt-2">
            Get Started
          </button>
        </nav>
      </div>
    </header>
  );
};

export default Header;
